import React, { Component } from 'react';
import DashboardData from '../DashboardData'
import {get} from '../../api_client';
import './Dashboard-styles.css';

function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

class Dashboard extends Component {

  constructor(props) {
    super(props);
    let user_id = sessionStorage.getItem('user_id')
    let resource = localStorage.getItem('resource_type') || 'electricity'
    this.state = {
      user_id: user_id,
      first: this.props.userData.first,
      avatar_url: "./fake_avatar_img.jpg",
      loading: true,
      resourceType: resource,
    };
    this.setDashboardData = this.setDashboardData.bind(this)
  };

  componentDidMount(){
    this.updateUserData()
  }

  setDashboardData(e){
    let type = e.target.name
    localStorage.setItem("resource_type", type)
    this.setState({
      resourceType: type,
    }, this.updateUserData)
  }

  updateUserData(){
    // t
    let type = this.state.resourceType
    const path = `api/v1/users/${this.state.user_id}/resources?resource=${type}`
    get(path)
      .then(data => this.setState({data, loading: false}))
      .catch(error => console.log(error))
  }

  render() {
    console.log(this.state)
    let loading = this.state.loading
    return (
      <div>
        <div className="dashboard-header-container">
          <div className="dashboard-overlay">
              <div className="avatar-container">
                <img
                  className="avatar-img"
                  alt="carbon collective logo"
                  src={require(`${this.state.avatar_url}`)} />
              </div>
              <div className="dashboard-title-container">
                <h6>{ `${this.state.first}'s Dash` }</h6>
                <button
                  className="update-bill-button"
                  onClick={()=> { this.props.history.push("/managebills") }}
                >
                  Manage {capitalizeFirstLetter(this.state.resourceType)} Bill
                </button>
              </div>
            </div>
            <div className="dashboard-resource-nav">
                <button
                className="resource-btn electricity-btn"
                onClick={this.setDashboardData}
                name="electricity"
                >Electricity
                </button>
                <button
                className="resource-btn water-btn"
                onClick={this.setDashboardData}
                name="water"
                >Water
                </button>
                <button
                className="resource-btn gas-btn"
                onClick={this.setDashboardData}
                name="gas"
                >Gas
                </button>
                <button
                className="resource-btn carbon-btn"
                onClick={this.setDashboardData}
                name="carbon"
                >Carbon
                </button>
            </div>
          <div>
          </div>
        </div>
        <div className="dashboard-data-container">
          <div className="divider">
          </div>
          {!loading ? <DashboardData {...this.state} updateState={this.props.updateState}/> : null }
        </div>
      </div>
    )
  };
};

export default Dashboard;



